"use client";

import { useEffect, useState } from "react";
import { useRouter } from "next/router"; // To handle navigation
import { useSession } from "next-auth/react"; // Assuming you're using next-auth for authentication

const PrivateRoute = ({ children }: { children: React.ReactNode }) => {
  const { data: session, status } = useSession(); // Use session from next-auth or your custom auth hook
  const router = useRouter();
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    if (status === "loading") {
      // While loading the session, show the content
      return;
    }

    if (!session) {
      // If there's no session, redirect to login page
      router.push("/login");
    } else {
      setLoading(false); // If session exists, stop loading and render children
    }
  }, [session, status, router]);

  if (loading) {
    return <div>Loading...</div>; // Show a loading state while checking session
  }

  return <>{children}</>; // If the user is logged in, render the children (the protected page content)
};

export default PrivateRoute;
